export default {
    init: function() {
        let pageViewer = document.getElementById("collection-items-list")
        if(!pageViewer) return
        console.log("found")
        let documentId = pageViewer.dataset.documentId; 
        fetch("https://dolphin-app-p9llh.ondigitalocean.app/solr/#/blacklight/query?q=serial_key:"+documentId+"&rows=15000&useParams=", {
            method: 'GET'
        })
        .then((response) => response.json())
        .then(data => {
           print(data)
        })
    }
}